import { getSelfInfoStorage, logout, signin, bugfix, freeLuckyDraw } from "./auth";

export const initContentMenus = async () => {
	await resetContextMenus();
};


let resetContextMenusIng = false;

export const resetContextMenus = async () => {
	if (resetContextMenusIng) return;
	resetContextMenusIng = true;
	// 
	await chrome.contextMenus.removeAll();
	// 
	let self = await getSelfInfoStorage();
	// 
	if (self) {
		await chrome.contextMenus.create({
			id: "MENU_PARENT",
			title: `${self.user_basic.user_name}`,
			contexts: ["all"]
		});
		await chrome.contextMenus.create({
			id: "SELF_HOME",
			title: "",
			parentId: "MENU_PARENT",
			contexts: ["all"]
		});
		await chrome.contextMenus.create({
			id: "SELF_NOTIFICATION",
			title: "",
			parentId: "MENU_PARENT",
			contexts: ["all"]
		});
		await chrome.contextMenus.create({
			id: "separator1",
			type: "separator",
			parentId: "MENU_PARENT",
			contexts: ["all"]
		});
		await chrome.contextMenus.create({ id: "SIGN_IN", title: "", parentId: "MENU_PARENT", contexts: ["all"] });
		await chrome.contextMenus.create({ id: "BUG_FIX", title: "BUG", parentId: "MENU_PARENT", contexts: ["all"] });
		await chrome.contextMenus.create({
			id: 'FREE_LUCKY_DRAW',
			title: "",
			parentId: "MENU_PARENT",
			contexts: ["all"]
		})
		await chrome.contextMenus.create({
			id: "separator2",
			type: "separator",
			parentId: "MENU_PARENT",
			contexts: ["all"]
		});
		await chrome.contextMenus.create({ id: "LOGOUT", title: "", parentId: "MENU_PARENT", contexts: ["all"] });
	} else {
		await chrome.contextMenus.create({ id: "OPEN_JUEJIN", type: "normal", title: ``, contexts: ["all"] });
	}
	resetContextMenusIng = false;
};

export const onClick = async (info, tab) => {
	let self = await getSelfInfoStorage();
	let { windowId } = tab;
	let { menuItemId } = info;
	if (menuItemId === "OPEN_JUEJIN") { // 
		await chrome.tabs.create({ url: `https://juejin.cn/`, selected: true, windowId });
	}
	if (menuItemId === "SELF_HOME") { // 
		await chrome.tabs.create({
			url: `https://juejin.cn/user/${self.user_basic.user_id}`,
			selected: true,
			windowId
		});
	}
	if (menuItemId === "SELF_NOTIFICATION") { // 
		await chrome.tabs.create({ url: `https://juejin.cn/notification`, selected: true, windowId });
	}
	if (menuItemId === "LOGOUT") { // 
		await logout();
	}
	if (menuItemId === "SIGN_IN") { // 
		await signin();
	}
	if (menuItemId === "BUG_FIX") { // bug
		await bugfix();
	}
	if (menuItemId === 'FREE_LUCKY_DRAW') { // 
		await freeLuckyDraw()
	}
};
